<template lang="pug">
v-app
  v-app-bar#navbar.z-100(
      app

      prominent
      height='196'

    )
    .w-100.sd-color
      v-container#navtext-container.xl-mw.pa-0
        p.mb-0.text-subtitle-2.white--text
          |  $199  $499 ()
    .w-100
      v-container#header-container.xl-mw.pa-0.d-flex.align-center
        router-link.mr-12(
          to='/'
        )
          v-img(
            :src="require('@/assets/logo.png')"
            contain
            max-width='125'
          )
        v-text-field.flex-grow-0.align-center.mr-auto(
          :class='{ noresult: this.$store.state.product.searchBar.length > 0 && this.$store.state.product.keywords.length === 0 }'
          dense
          outlined
          color='#000000'
          @input='search(0)'
          v-model='keywords'
          @change='search(0)'
          @click:append='search(0)'
          @keydown.enter='search(0)'
          placeholder='//'
        )
          template(v-slot:append)
            v-btn(
              icon
              @click='search(0)'
            )
              v-icon mdi-magnify
        .d-flex.align-center
          v-btn.ml-3(
            depressed
            to='/register'
            v-if='!user.isLogin'
          )
            v-icon.mr-1(color) mdi-account-outline
            p.mb-0.font-weight-light 
          v-btn.ml-3(
            depressed
            to='/login'
            v-if='!user.isLogin'
          )
            v-icon.mr-1(color) mdi-account-outline
            p.mb-0.font-weight-light 
          v-btn.ml-3(
            depressed
            to='/admin'
            v-if='user.isLogin && user.isAdmin'
          )
            v-icon.mr-1(color) mdi-account-outline
            p.mb-0.font-weight-light 
          v-btn.ml-3(
            depressed
            @click='logout'
            v-if='user.isLogin'
          )
            v-icon.mr-1(color) mdi-account-outline
            p.mb-0.font-weight-light 
          v-btn.ml-3(
            depressed
            v-if='user.isLogin'
            to='/orders'
          )
            v-icon.mr-1(color) mdi-account-outline
            p.mb-0.font-weight-light 
          v-btn.ml-3(
            depressed
            to='/cart'
            v-if='user.isLogin'
          )
            v-icon.mr-1(color) mdi-cart-outline
            p.mb-0.font-weight-light(v-if='user.cart === 0') 
            v-badge(
              color='error'
              :content='user.cart'
              offset-x='-4'
              offset-y='0'
              v-if='user.cart > 0'
            ) 
      v-divider.divider
    .w-100
      v-container#menu-container.pa-0
        div
          v-tabs(
            color='maincolor'
            slider-color="maincolor"
            v-model='active_tab'
            show-arrows
            hide-slider
          )
            v-tab.px-0.self-tab-item(
              v-for='tab in tabs'
              :key='tab.id'
              :to='tab.to'
              exact-active-class='menu-tab-text'
              append
              @click='clearSearchBar(tab.id)'
            )
              span {{ tab.title }}
              v-divider(inset vertical)
    .w-100.test-color.z-50
      v-container#menutext-container.xl-mw.pa-0
        p.mb-0.text-subtitle-2.white--text.text-center
          | HIVER  8  3/4 
  v-main#main
    vue-page-transition(name='fade-in-right')
      //-  id 
      router-view(:key='$route.fullPath')
  v-footer.mt-5(color='darkbg' padless)
    v-row(justify='center' no-gutters)
      v-btn.my-2(v-for='link in links' :key='link' color='white' text rounded).
        {{ link }}
      v-col.py-4.text-center.grey--text.text--lighten-1(cols='12')
        | {{ new Date().getFullYear() }} 
        strong  Copyright  Justin Yu
        br
        p.grey--text.text--darken-1 &nbsp; | &nbsp;
</template>

<script>

export default {
  name: 'App',
  components: {
    // Navbar
  },
  data () {
    return {
      btns: [
        { icon: 'mdi-account-outline', text: '', to: '/register' },
        { icon: 'mdi-account-outline', text: '', to: '/login' },
        { icon: 'mdi-account-outline', text: '' },
        { icon: 'mdi-cart-outline', text: '(0)', to: '/cart' }
      ],
      //  tab
      active_tab: 0,
      tabs: [
        { id: 1, title: '', to: '/shop' },
        { id: 2, title: '', to: '/shop/hiking' },
        { id: 3, title: '', to: '/shop/ski' },
        { id: 4, title: '', to: '/shop/coat' },
        { id: 5, title: '', to: '/shop/male' },
        { id: 6, title: '', to: '/shop/female' },
        { id: 7, title: '', to: '/shop/camping' }
      ],
      links: [
        'Home',
        'About Us',
        'Team',
        'Services',
        'Blog',
        'Contact Us'
      ]
    }
  },
  computed: {
    keywords: {
      // v-model  Vuex  module 
      get () {
        return this.$store.state.product.searchBar
      },
      set (value) {
        this.$store.commit('product/updateKeywords', value)
      }
    }
  },
  methods: {
    clearSearchBar (value) {
      if (value > 1) {
        this.$store.commit('product/clear')
      }
    },
    search (value) {
      if (value === 0) {
        this.$store.dispatch('product/search', this.keywords)
        this.$router.push('/shop')
        return
      }
      this.$store.dispatch('product/search', this.keywords)
    },
    logout () {
      this.$swal.fire({
        title: '',
        text: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: '',
        cancelButtonText: ''
      }).then((result) => {
        if (result.isConfirmed) {
          this.$store.dispatch('user/logout')
        }
      })
    }
  },
  async created () {
    this.$store.dispatch('user/getInfo')
  },
  watch: {
    keywords: {
      deep: true,
      handler (newValue, oldValue) {
        console.log(newValue, oldValue)
        if (this.keywords.length > 0 || (oldValue === 1) || (newValue === 1)) {
          this.$router.push('/shop')
        }
      }
    }
  }
}
</script>
